
.overlay{
    position: fixed;
    z-index: 3;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.overlay2{
    background-color: rgb(0, 14, 31,0.7);
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.overlay3{
    position: fixed;
    z-index: 6;
    justify-content: center;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 95%;
    width: 95%;
    background-color: white;
    border-radius: 1rem;
}

.Modal{
    position: fixed;
    z-index: 6;
    justify-content: center;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 95%;
    width: 95%;
    background-color: white;
    border-radius: 1rem;
    overflow: auto;

    
}


.topBar{
    position: fixed;
    z-index: 7;
    top: 0%;
    display: flex;
    justify-content: center;
    height: 56px;
    padding: 0.2rem;
    padding-top: 0.8rem;
    width: 100%;
    background-color:rgb(194, 241, 255);
    border-radius: 1rem 1rem 0px 0px;

}
.space{
    width: 33%;
    height: 100%;
    float: left;
}

.title2{
    text-align: center;
    width: 33%;
    font-size: 3vh;
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
}

.exitbutton{
    display: flex;
    cursor: pointer;
    width: 33%;
}

.icon{
    height: 100%;
    width: auto;
    margin-left: auto; 
    margin-right: 0;
}

.text{
    display: flex;
    text-align: left;
    font-size: 2.2vh;
    margin-right: auto;
    margin-left: auto;
    max-width: 50rem;
    margin-top: 54px;
}